.App {
    font-family: sans-serif;
    text-align: center;
  }
  
  .modal-backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
  }
  
  .modal-content {
    width: 60%;
    min-height: 200px;
    background-color: white;
    padding: 25px;
  }
  